<template>
    <User v-if="isLoggedIn" />
    <div v-if="!isLoggedIn" class="jumbotron">
      <h1 class="display-4">Hello!</h1>
      <hr class="my-4">
      <a class="btn btn-primary" href="https://id.twitch.tv/oauth2/authorize?client_id=qkc5eid1708t3j2ms0nswbw6gkmqw7&redirect_uri=https://ybww9nzma4.execute-api.us-east-2.amazonaws.com/main/getToken&response_type=code">Log in with Twitch</a>
    </div>
</template>

<script lang="ts">
// import { onMounted } from '@vue/runtime-core'
import { useStore } from 'vuex'
import { computed } from 'vue'
import User from '../components/User.vue'

export default {
  setup() {
    const store = useStore()
    const setLastError = (error: any) => { store.commit('setError', error) }
    if (store.getters.hasSid && !store.state.loggedIn) {
      store.dispatch('logIn')
    }
    const isLoggedIn = computed(() => useStore().state.loggedIn)
    return { setLastError, isLoggedIn }
  },
  components: {
    User
  },
  name: 'Home'
}

</script>
