import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    error: {},
    user: null,
    loggedIn: false,
    sid: window.localStorage.getItem('sid')
  },
  getters: {
    hasSid(state) {
      return state.sid !== undefined && state.sid != null
    }
  },
  mutations: {
    setError(state, error) {
      state.error = error
    },
    logIn(state, user) {
      state.user = user
      state.loggedIn = true
    },
    logOut(state) {
      state.loggedIn = false
    },
    removeUser(state) {
      state.user = null
    },
    setSid(state, sid) {
      state.sid = sid
      window.localStorage.setItem('sid', sid)
    },
    removeSid(state) {
      state.sid = null
      window.localStorage.removeItem('sid')
    }
  },
  actions: {
    logIn({ commit, state }) {
      console.log(`logging in, ${state.sid}`)
      commit('setSid', state.sid)
      axios.get('https://ybww9nzma4.execute-api.us-east-2.amazonaws.com/main/getCurrentUser', {
          headers: {
            token: state.sid
          }
        })
        .then(response => {
          console.log(response.status)
          if (response.status === 200) {
            commit('logIn', response.data)
            // response.data
          }
        })
        .catch(error => {
          commit('setError', error)
        })
    },
    logOut({ commit }) {
      commit('logOut')
      commit('removeUser')
      commit('removeSid')
    }
  },
  modules: {
  }
})
